import React, { useEffect } from 'react';

const Scene3: React.FC = () => {

  useEffect(() => {
    const imgs: NodeListOf<HTMLImageElement> = document.querySelectorAll('img.mouseover');

    imgs.forEach(img => {
      img.addEventListener('mouseover', mouseOverHandler, true);
      img.addEventListener('mouseleave', mouseLeaveHandler, true);
    });

    return () => {
      imgs.forEach(img => {
        img.removeEventListener('mouseover', mouseOverHandler, true);
        img.removeEventListener('mouseleave', mouseLeaveHandler, true);
      });
    }
  }, []);

  const mouseOverHandler = (e: MouseEvent) => {
    e.preventDefault();
    const img = e.target as HTMLImageElement;

    img.setAttribute('data-src', img.src);
    img.setAttribute('src', img.dataset['mouseover']!);
  };

  const mouseLeaveHandler = (e: MouseEvent) => {
    e.preventDefault();
    const img = e.target as HTMLImageElement;

    img.setAttribute('data-mouseover', img.src);
    img.setAttribute('src', img.dataset['src']!);
  };

  return (
    <main className='Scene3'>
      <img className='icon mouseover' style={{ top: '416px', left: '312px' }}
           src='/assets/imgs/in/refrigerator/strawberry-milk.png'
           data-mouseover='/assets/imgs/in/mouseover/strawberry-milk.png' />

      <img className='icon mouseover' style={{ top: '413px', left: '552px' }}
           src='/assets/imgs/in/refrigerator/blue-milk.png'
           data-mouseover='/assets/imgs/in/mouseover/blue-milk.png' />

      <img className='icon mouseover' style={{ top: '546px', left: '320px' }}
           src='/assets/imgs/in/refrigerator/sprite.png'
           data-mouseover='/assets/imgs/in/mouseover/sprite.png' />

      <img className='icon mouseover' style={{ top: '548px', left: '555px' }}
           src='/assets/imgs/in/refrigerator/coke.png'
           data-mouseover='/assets/imgs/in/mouseover/coke.png' />

      <img className='icon mouseover' style={{ top: '701px', left: '449px' }}
           src='/assets/imgs/in/refrigerator/water.png'
           data-mouseover='/assets/imgs/in/mouseover/water.png' />

      <img className='icon mouseover' style={{ top: '701px', left: '552px' }}
           src='/assets/imgs/in/refrigerator/water.png'
           data-mouseover='/assets/imgs/in/mouseover/water.png' />

      <img className='icon door' style={{ top: '246px', left: '288px', transition: 'transform 0.3s ease-in-out' }}
           src='/assets/imgs/in/refrigerator/door.png'
           data-mouseover='/assets/imgs/in/mouseover/door.png'
           onMouseOver={(e) => {
             const target = e.target as HTMLDivElement;
             console.log(target.childNodes[0]);
             // target.style.transform = 'translateX(210px)';
           }}
           // onMouseLeave={(e) => {
           //   const target = e.target as HTMLImageElement;
           //   target.style.transform = 'translateX(0)';
           // }}
      />

      <div className='door-wrapper door-wrapper--right'
           onMouseEnter={(e) => {
             e.preventDefault();
             const target = e.target as HTMLDivElement;
             const door = target.childNodes[0] as HTMLImageElement;
             door.style.transform = 'translateX(-210px)';
           }}
           onMouseLeave={(e) => {
             const target = e.target as HTMLImageElement;
             const door = target.childNodes[0] as HTMLImageElement;
             door.style.left = 'translateX(0)';
           }}
      >
        <img className='icon door' style={{ transition: 'transform 0.3s ease-in-out' }}
             src='/assets/imgs/in/refrigerator/door.png'
             data-mouseover='/assets/imgs/in/mouseover/door.png'
             onMouseEnter={undefined}
        />
      </div>

      <img className='icon mouseover' style={{ top: '207px', left: '1185px' }}
           src='/assets/imgs/in/mede/tteokbokki.png'
           data-mouseover='/assets/imgs/in/mouseover/tteokbokki.png' />

      <img className='icon mouseover' style={{ top: '709px', left: '818px' }}
           src='/assets/imgs/in/mede/nemoni.png'
           data-mouseover='/assets/imgs/in/mouseover/nemoni.png' />

      <img className='icon mouseover' style={{ top: '391px', left: '1196px'}}
           src='/assets/imgs/in/mede/doong.png'
           data-mouseover='/assets/imgs/in/mouseover/doong.png' />

      <img className='icon mouseover' style={{ top: '475px', left: '818px' }}
           src='/assets/imgs/in/mede/cream-bread.png'
           data-mouseover='/assets/imgs/in/mouseover/cream-bread.png' />

      <img className='icon mouseover' style={{ top: '475px', left: '976px'}}
           src='/assets/imgs/in/mede/red-bean-bread.png'
           data-mouseover='/assets/imgs/in/mouseover/red-bean-bread.png' />

      <img className='icon mouseover' style={{ top: '304px', left: '849px' }}
           src='/assets/imgs/in/mede/tibisket.png'
           data-mouseover='/assets/imgs/in/mouseover/tibisket.png' />

      <style jsx>{`
        .Scene3 {
          display: block;
          position: fixed;
          width: 1920px;
          height: 980px;
          background-image: url('/assets/imgs/in/background.png');
          background-repeat: no-repeat;
          background-size: contain;
          background-position: center;
        }
        .door-wrapper {
          position: absolute;
          width: 241px;
          height: 576px;
          cursor: pointer;

          z-index: 1;
        }
        .door-wrapper--left {
        }
        .door-wrapper--right {
          top: 246px;
          left: 527px;
        }
        .door {
          pointer-events: none;
        }
        .icon {
          position: absolute;
          width: fit-content;
          heigth: fit-content;
          object-fit: contain;
          object-position: center;
          cursor: pointer;
        }
      `}

      </style>
    </main>
  )
};
      
export default Scene3;
